@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';

.skillsBlock {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;


  .fade{
    @media screen and (min-width: 450px) {
      width: 80%;
    }
    .skillsContainer {
      @include container();
      display: flex;
      align-items: center;
      flex-direction: column;

      .skills {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        padding: 40px 0 0;
        @media screen and (max-width: 750px) {
          display: block;
        }
      }

    }
  }


}




